import { createStore } from "redux";

const initData = {
  taskList: []
};

export function reducer(state = initData, action) {
  switch (action.type) {
    case "ADD":
      return addReduce(state, action);
    case "DELETE":
      return delReduce(state, action);
    case "UP":
      return upReduce(state, action);
    default:
      return state;
  }
}

function addReduce(state, action) {
  let addData = {
    task: action.task,
    // limit: action.limit == null ? "" : action.limit.toLocaleDateString()
    limit:
      action.limit == null
        ? ""
        : action.limit.getMonth() + 1 + "/" + action.limit.getDate()
  };

  let taskList = state.taskList.slice();
  taskList.unshift(addData);
  return {
    taskList: taskList
  };
}

function delReduce(state, action) {
  let taskList = state.taskList.slice();
  taskList.splice(action.index, 1);

  return {
    taskList: taskList
  };
}

function upReduce(state, action) {
  let taskList = state.taskList;
  let newTaskList;

  if (action.index > 0) {
    let up = taskList.slice(action.index, action.index + 1);
    taskList.splice(action.index, 1); //delete
    let head = taskList.slice(0, action.index - 1);
    let tail = taskList.slice(action.index - 1, taskList.length);
    newTaskList = head.concat(up, tail);
  } else {
    newTaskList = state.taskList.slice();
  }

  return {
    taskList: newTaskList
  };
}

export function addTask(task, limit) {
  return {
    type: "ADD",
    task: task,
    limit: limit
  };
}

export function delTask(index) {
  return {
    type: "DELETE",
    index: index
  };
}

export function upTask(index) {
  return {
    type: "UP",
    index: index
  };
}

export default createStore(reducer);
