import React, { Component } from "react";
import { connect } from "react-redux";
import DelForm from "./DelForm";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import { upTask } from "./Store";

class Item extends Component {
  constructor(props) {
    super(props);
    this.doAction = this.doAction.bind(this);
  }

  doAction(e) {
    e.preventDefault();
    let action = upTask(this.props.index);
    this.props.dispatch(action);
  }

  render() {
    return (
      <TableRow>
        <TableCell>
          <DelForm index={this.props.index} />
        </TableCell>
        <TableCell onClick={this.doAction}>{this.props.data.task}</TableCell>
        <TableCell>{this.props.data.limit}</TableCell>
      </TableRow>
    );
  }
}

export default connect(state => state)(Item);
