import React, { Component } from "react";
import { connect } from "react-redux";
import { addTask } from "./Store";
import Button from "@material-ui/core/Button";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";
import { AddCircle } from "@material-ui/icons";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";

class AddForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addTask: "",
      addTaskLimit: new Date()
    };
    this.doChangeTask = this.doChangeTask.bind(this);
    this.doChangeLimit = this.doChangeLimit.bind(this);
    this.doAction = this.doAction.bind(this);
  }

  doChangeTask(e) {
    this.setState({
      addTask: e.target.value
    });
  }

  doChangeLimit(date) {
    this.setState({
      addTaskLimit: date
    });
  }

  doAction(e) {
    e.preventDefault();
    let action = addTask(this.state.addTask, this.state.addTaskLimit);
    this.props.dispatch(action);
    this.setState({
      addTask: "",
      addTaskLimit: this.state.addTaskLimit
    });
  }

  render() {
    return (
      <div>
        <form onSubmit={this.doAction}>
          <Container maxWidth="xs">
            <Grid container spacing={1}>
              <Grid item xs={12}>
                <TextField
                  label="New Task"
                  variant="outlined"
                  onChange={this.doChangeTask}
                  value={this.state.addTask}
                  fullWidth
                  required
                />
              </Grid>
              <Grid item xs={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    disablePast
                    label="Limit"
                    inputVariant="outlined"
                    // invalidDateMessage
                    value={this.state.addTaskLimit}
                    onChange={this.doChangeLimit}
                    format="MM/dd"
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={6}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  startIcon={<AddCircle />}
                >
                  Add
                </Button>
              </Grid>
            </Grid>
          </Container>
        </form>
      </div>
    );
  }
}

export default connect(state => state)(AddForm);
